import React, { useEffect, useRef } from 'react';
import fotomarco from '../img/fotoenmarco.png';
import atenas3 from '../img/atenas3.jpg';
import lottie from 'lottie-web';
import {IntlProvider, FormattedMessage} from 'react-intl';

const Marketers = () => {

    const efecto = useRef(null)

    useEffect(() => {
        lottie.loadAnimation({
            container: efecto.current,
            renderer: 'svg',
            loop: true,
            autoplay: true,
            animationData: require('../img/95758-fire.json')
        })

        /* -----slider1----- */
        const root = document.documentElement;
        const marqueeElementsDisplayed = getComputedStyle(root).getPropertyValue("--marquee-elements-displayed");
        const marqueeContent = document.querySelector("ul.marquee-content");
        root.style.setProperty("--marquee-elements", marqueeContent.children.length);
        for (let i = 0; i < marqueeElementsDisplayed; i++) {
            marqueeContent.appendChild(marqueeContent.children[i].cloneNode(true));
        }

        /* -----slider2----- */
        const root2 = document.documentElement;
        const marqueeElementsDisplayed2 = getComputedStyle(root2).getPropertyValue("--marquee-elements-displayed2");
        const marqueeContent2 = document.querySelector("ul.marquee-content2");

        root2.style.setProperty("--marquee-elements2", marqueeContent2.children.length);

        for (let i = 0; i < marqueeElementsDisplayed2; i++) {
            marqueeContent2.insertAdjacentElement('afterbegin', marqueeContent2.children[i].cloneNode(true))
            marqueeContent2.insertAdjacentElement('afterbegin', marqueeContent2.children[i].cloneNode(true))
        }

    })

    return <>
        <section className="marketers">
            <div className="container containermarketers">
                <div className="content">
                    <div className="part1" data-aos="fade-right">
                        <div className="title">
                            <h2><FormattedMessage id="titulo-marketers" /></h2>
                        </div>
                        <p><FormattedMessage id="text-marketers-1" />
                        </p>
                        <p><FormattedMessage id="text-marketers-2" />
                        </p>
                        <div className="empecemos">
                            <a href="#contact"><FormattedMessage id="btn-marketers" /></a>
                        </div>
                    </div>
                    <div className="part2" data-aos="fade-left">
                        <img src={atenas3} alt="foto-perfil" />
                    </div>
                </div>
            </div>               
                <div className="marquee">
                    <ul className="marquee-content">
                        <li>hacemos creamos desarrollamos</li>
                        <li>hacemos creamos desarrollamos</li>
                    </ul>
                </div>
                <div className="marquee">
                    <ul className="marquee-content marquee-content2">
                        <li>pginas web logotipos ecommerce</li>
                        <li>pginas web logotipos ecommerce</li>
                    </ul>
                </div>
        </section>
    </>;
};

export default Marketers;