import React, {useContext} from 'react';
import menubtn from '../img/menu.png';
import logo from '../icons/logo-atenas.svg';
import { langContext } from './../context/langContext';
import {IntlProvider, FormattedMessage} from 'react-intl';

const Menupage = () => {
    const idioma2 = useContext(langContext);

    const handleToggleMenu1 = (e) => {
        if (e.target.closest(".menubtn")) {
            document.querySelector(".menupage").classList.toggle("open");
            document.querySelector("body").classList.toggle("scrollhidden");
        }
    }

    const handleToggleMenu2 = (e) => {
        if (e.target.closest(".cerrarr")) {
            document.querySelector(".menupage").classList.toggle("open");
            document.querySelector("body").classList.toggle("scrollhidden");
        }
    }

    document.addEventListener("click", (e) => {
        if (e.target.closest("button.es")) {        
            document.getElementById('one').style.display = 'block';
            document.getElementById('two').style.display = 'none';        
            document.querySelector(".menupage").classList.remove("open");
            document.querySelector("body").classList.remove("scrollhidden"); 
            document.querySelector(".li-button-es").classList.add("active"); 
            document.querySelector(".li-button-en").classList.remove("active"); 
            // document.getElementById('btn-one').style.display = 'block';
            // document.getElementById('btn-two').style.display = 'none';
        }
        if (e.target.closest("button.en")) {
            document.getElementById('one').style.display = 'none';
            document.getElementById('two').style.display = 'block';
            document.querySelector(".menupage").classList.remove("open");
            document.querySelector("body").classList.remove("scrollhidden"); 
            document.querySelector(".li-button-es").classList.remove("active"); 
            document.querySelector(".li-button-en").classList.add("active"); 
            // document.getElementById('btn-one').style.display = 'none';
            // document.getElementById('btn-two').style.display = 'block';
        }
    })

    return <>
        <section className="menupage">
            <div className="container">
                <div className="top">
                    <img src={logo} alt="logo" />
                    <button className="menubtn" onClick={handleToggleMenu1}>
                        <img src={menubtn} alt="logo" />
                    </button>
                </div>
                <div className="content">
                    <ul>
                        <li><a href="#home" className="cerrarr" onClick={handleToggleMenu2}>Home</a></li>
                        <li><a href="#servicios" className="cerrarr" onClick={handleToggleMenu2}><FormattedMessage id="services-navbar" /></a></li>
                        <li><a href="#portafolio" className="cerrarr" onClick={handleToggleMenu2}><FormattedMessage id="portfolio-navbar" /></a></li>
                        <div className="select-idioma">
                            <li className="idioma li-button-es">
                                <button onClick={() => idioma2.establecerLenguaje('es-ES')} className="es">ES</button>
                            </li>
                            <li className="li-button-en">
                                <button onClick={() => idioma2.establecerLenguaje('en-US')} className="en">EN</button>
                            </li>
                        </div>
                    </ul>
                </div>
            </div>
        </section>
    </>;
};

export default Menupage;