import React, {useContext} from 'react';
import logo from '../icons/logo-atenas.svg'
import menu from '../img/menu.png'
import {IntlProvider, FormattedMessage} from 'react-intl';
import { langContext } from './../context/langContext';

const Navbar = () => {
    const idioma = useContext(langContext);

    const enlaces = document.querySelectorAll('.idioma button');

    enlaces.forEach((elemento) => {
        elemento.addEventListener('click', (evento) => {
            enlaces.forEach((enlace) => enlace.classList.remove('active'));
            evento.target.classList.add('active');
        })
    })
    document.addEventListener("click", (e) => {
        if (e.target.closest(".idioma button.es")) {        
            document.getElementById('one').style.display = 'block';
            document.getElementById('two').style.display = 'none';            
            // document.getElementById('btn-one').style.display = 'block';
            // document.getElementById('btn-two').style.display = 'none';
        }
        if (e.target.closest(".idioma button.en")) {
            document.getElementById('one').style.display = 'none';
            document.getElementById('two').style.display = 'block';
            // document.getElementById('btn-one').style.display = 'none';
            // document.getElementById('btn-two').style.display = 'block';
        }
    })

    const handleToggleMenu = (e) => {
        if (e.target.closest(".menubtn")) {
            document.querySelector(".menupage").classList.toggle("open");
            // jQuery('body').addClass('scrollhidden');
            document.querySelector("body").classList.toggle("scrollhidden");
        }
    }

    return <>
        <header>
            <nav className="container" data-aos="fade-down">
                <div className="logo">
                    <a href="http://amdigital.media/"
                    ><img src={logo} alt="Logo-AMDIGITAL" /></a
                    >
                </div>
                <div className="navigation">
                    <ul>
                        <li>
                            <a href="#servicios" className="hvr-underline-from-center"
                            ><FormattedMessage id="services-navbar" /></a
                            >
                        </li>
                        <li className="menuPortafolio">
                            <span><FormattedMessage id="text-portfolio-navbar" /></span>
                            <a href="#portafolio" className="hvr-underline-from-center"
                            ><FormattedMessage id="portfolio-navbar" /></a
                            >
                        </li>
                        <li className="idioma">
                            <button onClick={() => idioma.establecerLenguaje('es-ES')} className="es idioma-btn hvr-underline-from-center active">ES</button>
                            <button onClick={() => idioma.establecerLenguaje('en-US')} className="en idioma-btn hvr-underline-from-center">EN</button>
                        </li>
                    </ul>
                </div>
                <button className="menubtn" onClick={handleToggleMenu}>
                    <img src={menu} className="menubar" alt="menu" />
                </button>
            </nav>
        </header>
    </>;
};

export default Navbar;