import React from 'react';
import item1 from '../img/item-1.png';
import item2 from '../img/item-2.png';
import item3 from '../img/item-3.png';
import item4 from '../img/item-4.png';
import {IntlProvider, FormattedMessage} from 'react-intl';

const portfolio = () => {
    return <>
        <section id="portafolio" className="portfolio">
            <div className="container">
                <div className="content-portfolio">
                    <div className="title">
                        <h2 data-aos="fade-down"><FormattedMessage id="title-porftfolio" /></h2>
                        <p data-aos="fade-up"><FormattedMessage id="bajada-porftfolio" /></p>
                    </div>
                    <div className="items">
                        <div className="item">
                            <div className="img">
                                <img src={item1} alt="" />
                            </div>
                            <div className="info">
                                <p>Diseo UX, Diseo UI, Creatividad</p>
                                <h3>NERD - Digital Minds</h3>
                            </div>
                        </div>
                        <div className="item">
                            <div className="img">
                                <img src={item2} alt="" />
                            </div>
                            <div className="info">
                                <p>Diseo UX, Diseo UI, Creatividad</p>
                                <h3>Wedding Video Club</h3>
                            </div>
                        </div>
                        <div className="item">
                            <div className="img">
                                <img src={item3} alt="" />
                            </div>
                            <div className="info">
                                <p>Diseo UX, Diseo UI, Creatividad</p>
                                <h3>Alonso Digital</h3>
                            </div>
                        </div>
                        <div className="item">
                            <div className="img">
                                <img src={item4} alt="" />
                            </div>
                            <div className="info">
                                <p>Diseo UX, Diseo UI, Creatividad</p>
                                <h3>Nerd Media S.A.C.</h3>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </>;
};

export default portfolio;