import React from 'react';
import icondiseno from '../icons/icon-diseo.svg';
import iconweb from '../icons/icon-web.svg';
import icondiseno1 from '../icons/icon-diseo1.svg';
import iconweb1 from '../icons/icon-web1.svg';
import {IntlProvider, FormattedMessage} from 'react-intl';

const services = () => {
    return <>
        <section id="servicios" className="services">
            <div className="container">
                <div className="content-services">
                    <div className="title">
                        <h2 data-aos="fade-down"><FormattedMessage id="title-services" /></h2>
                        <p className="bajada" data-aos="fade-up"><FormattedMessage id="bajada-services" /></p>
                    </div>
                    <div className="items-services">
                        <div className="item-services">
                            <div className="img-services" data-aos="fade-right">
                                <img src={icondiseno1} alt="logo" />
                            </div>
                            <div className="title-services" data-aos="fade-right">
                                <h3><FormattedMessage id="title-services-1" /></h3>
                            </div>
                            <div className="info-services" data-aos="fade-left">
                                <p><FormattedMessage id="descript-services-1" /></p>
                                <div className="lista">
                                    <ul>
                                        <li><FormattedMessage id="lista-services-1-1" /></li>
                                        <li><FormattedMessage id="lista-services-1-2" /></li>
                                        <li><FormattedMessage id="lista-services-1-3" /></li>
                                        <li><FormattedMessage id="lista-services-1-4" /></li>
                                        <li><FormattedMessage id="lista-services-1-5" /></li>
                                    </ul>
                                    <ul>
                                        <li><FormattedMessage id="lista-services-1-6" /></li>
                                        <li><FormattedMessage id="lista-services-1-7" /></li>
                                        <li><FormattedMessage id="lista-services-1-8" /></li>
                                        <li><FormattedMessage id="lista-services-1-9" /></li>
                                    </ul>
                                </div>

                                <a href="#contact" className="empecemos2"
                                ><FormattedMessage id="Title-btn" /></a
                                >
                            </div>
                        </div>
                        <div className="item-services">
                            <div className="img-services" data-aos="fade-right">
                                <img src={iconweb1} alt="logo" />
                            </div>
                            <div className="title-services" data-aos="fade-right">
                                <h3><FormattedMessage id="title-services-2" /></h3>
                            </div>
                            <div className="info-services" data-aos="fade-left">
                                <p><FormattedMessage id="descript-services-2" /></p>
                                <div className="lista">
                                    <ul>
                                        <li><FormattedMessage id="lista-services-2-1" /></li>
                                        <li><FormattedMessage id="lista-services-2-2" /></li>
                                        <li><FormattedMessage id="lista-services-2-3" /></li>
                                        <li><FormattedMessage id="lista-services-2-4" /></li>
                                    </ul>
                                    <ul>
                                        <li><FormattedMessage id="lista-services-2-5" /></li>
                                        <li><FormattedMessage id="lista-services-2-6" /></li>
                                        <li><FormattedMessage id="lista-services-2-7" /></li>
                                        <li><FormattedMessage id="lista-services-2-8" /></li>
                                        <li><FormattedMessage id="lista-services-2-9" /></li>
                                    </ul>
                                </div>
                                <a href="#contact" className="empecemos2"
                                ><FormattedMessage id="Title-btn" /></a
                                >
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </>;
};

export default services;