import React, { Component } from 'react';
import axios from 'axios';
import {IntlProvider, FormattedMessage} from 'react-intl';

const contact = () => {

    const submitForm = (e) => {

        e.preventDefault();
        var valorFullNameContact = 0;
        var valortelephoneContact = 0;
        var valoremailContact = 0;
        var valorselectContact = 0;
        var valormessageContact = 0;

        if (document.querySelector("#full-name").value.length > 0) {
            valorFullNameContact = 1;
            document.querySelector("#full-name").classList.remove("falto");
        }else{
            document.querySelector("#full-name").classList.add("falto");
        }
        
        if (document.querySelector("#telephone").value.length > 0) {
            valortelephoneContact = 1;
            document.querySelector("#telephone").classList.remove("falto");
        }else{
            document.querySelector("#telephone").classList.add("falto");
        }

        if (document.querySelector("#email").value.length > 0) {
            valoremailContact = 1;
            document.querySelector("#email").classList.remove("falto");
        }else{
            document.querySelector("#email").classList.add("falto");
        }

        if (document.querySelector("#select-servicio").value !== "Servicios solicitado:") {
            valorselectContact = 1;
            document.querySelector("#select-servicio").classList.remove("falto");
        }else{
            document.querySelector("#select-servicio").classList.add("falto");
        }

        if (document.querySelector("#message").value.length > 0) {
            valormessageContact = 1;
            document.querySelector("#message").classList.remove("falto");
        }else{
            document.querySelector("#message").classList.add("falto");
        }

        var result2 = valorFullNameContact * valortelephoneContact * valoremailContact * valorselectContact * valormessageContact;

        if (result2 == 0) {
            e.preventDefault();
        }else{
            document.querySelector("#fs-frm").submit();
        }
    }

    window.addEventListener('click', function (e) {

        if (e.target.closest("#full-name")) {
            document.querySelector("#full-name").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#full-name").parentElement.classList.remove("focusin");
            if (document.querySelector("#full-name").value.length > 0) {
                document.querySelector("#full-name").parentElement.classList.add("ok");
            } else {
                document.querySelector("#full-name").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#telephone")) {
            document.querySelector("#telephone").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#telephone").parentElement.classList.remove("focusin");
            if (document.querySelector("#telephone").value.length > 0) {
                document.querySelector("#telephone").parentElement.classList.add("ok");
            } else {
                document.querySelector("#telephone").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#email")) {
            document.querySelector("#email").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#email").parentElement.classList.remove("focusin");
            if (document.querySelector("#email").value.length > 0) {
                document.querySelector("#email").parentElement.classList.add("ok");
            } else {
                document.querySelector("#email").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#message")) {
            document.querySelector("#message").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#message").parentElement.classList.remove("focusin");
            if (document.querySelector("#message").value.length > 0) {
                document.querySelector("#message").parentElement.classList.add("ok");
            } else {
                document.querySelector("#message").parentElement.classList.remove("ok");
            }
        } 
    })

    document.addEventListener("keydown", function(event) {
        if (event.keyCode == 9) {
            document.querySelector("#full-name").addEventListener('focusin', (event) => {
                document.querySelector("#full-name").parentElement.classList.add("focusin");
            });
            document.querySelector("#full-name").addEventListener('focusout', (event) => {
                document.querySelector("#full-name").parentElement.classList.remove("focusin");
                if (document.querySelector("#full-name").value.length > 0) {
                    document.querySelector("#full-name").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#full-name").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#telephone").addEventListener('focusin', (event) => {
                document.querySelector("#telephone").parentElement.classList.add("focusin");
            });
            document.querySelector("#telephone").addEventListener('focusout', (event) => {
                document.querySelector("#telephone").parentElement.classList.remove("focusin");
                if (document.querySelector("#telephone").value.length > 0) {
                    document.querySelector("#telephone").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#full-name").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#email").addEventListener('focusin', (event) => {
                document.querySelector("#email").parentElement.classList.add("focusin");
            });
            document.querySelector("#email").addEventListener('focusout', (event) => {
                document.querySelector("#email").parentElement.classList.remove("focusin");
                if (document.querySelector("#email").value.length > 0) {
                    document.querySelector("#email").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#mail-home").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#message").addEventListener('focusin', (event) => {
                document.querySelector("#message").parentElement.classList.add("focusin");
            });
            document.querySelector("#message").addEventListener('focusout', (event) => {
                document.querySelector("#message").parentElement.classList.remove("focusin");
                if (document.querySelector("#message").value.length > 0) {
                    document.querySelector("#message").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#message").parentElement.classList.remove("ok");
                }
            });
        }
    })

    function handleChange (){
        if (document.querySelector("#full-name").value.length > 0) {
            document.querySelector("#full-name").parentElement.classList.add("ok");
        } else {
            document.querySelector("#full-name").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#telephone").value.length > 0) {
            document.querySelector("#telephone").parentElement.classList.add("ok");
        } else {
            document.querySelector("#telephone").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#email").value.length > 0) {
            document.querySelector("#email").parentElement.classList.add("ok");
        } else {
            document.querySelector("#email").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#message").value.length > 0) {
            document.querySelector("#message").parentElement.classList.add("ok");
        } else {
            document.querySelector("#message").parentElement.classList.remove("ok");
        }
    }

    function handleChange2 (){
        if (document.querySelector("#select-servicio").value !== "Servicios solicitado:") {
            document.querySelector("#select-servicio").classList.add("negro");
        }else{
            document.querySelector("#select-servicio").classList.remove("negro");
        }
    }
    
    return <>
        <section className="contact" id="contact">
            <div className="container">
                <div className="content-contact">
                    <h2 data-aos="fade-down"><FormattedMessage id="title-Contact" /></h2>
                    <p className="parrafo1" data-aos="fade-up">
                    <FormattedMessage id="bajada-Contact" />
                    </p>
                    <p className="parrafo2">
                    <FormattedMessage id="bajada-Contact-2" />
                    </p>
                    <div className="form" data-aos="fade-up">
                        <form className="formulario containerform" id="fs-frm"
                            // onSubmit={this.enviar}
                            name="complaint-form" acceptCharset="utf-8"
                            action="../php/thanks.php" method="post"
                        >
                            <fieldset>
                                <div className="form-group formname">
                                    <input type="text" name="full-name" id="full-name" placeholder="" required="" className="form-input" onChange={handleChange}
                                    // value={this.state.nombre} onChange={this.onChange} invalid={this.state.invalidNombre}
                                    />
                                    <label htmlFor="full-name" className="form-label"><FormattedMessage id="label-name-Contact" /></label>
                                </div>
                                <div className="form-group-column">
                                    <div className="form-group formtelefono">
                                        <input type="number" name="telephone" id="telephone" placeholder="" required="" className="form-input" onChange={handleChange}
                                    // value={this.state.telefono} onChange={this.onChange} invalid={this.state.invalidTelefono}
                                        />
                                        <label htmlFor="telephone" className="form-label"><FormattedMessage id="label-tel-Contact" /></label>
                                    </div>
                                    <div className="form-group formemail">
                                        <input type="email" name="email" id='email' placeholder="" className="form-input" onChange={handleChange} />
                                        <label htmlFor="email" className="form-label"><FormattedMessage id="label-email-Contact-home" /></label>
                                    </div>
                                </div>
                                <div className="form-group formselect">
                                    <select name="seelctservicio" id="select-servicio" onChange={handleChange2}>
                                        <option value="Servicios solicitado:"><FormattedMessage id="label-option1-Contact-home" /></option>
                                        <option value="Branding Diseo"><FormattedMessage id="label-option2-Contact-home" /></option>
                                        <option value="Desararrollo Web"><FormattedMessage id="label-option3-Contact-home" /></option>
                                    </select>
                                </div>
                                <div className="form-group formtextarea">
                                    <textarea rows="6" name="message" id="message" placeholder="" required="" className="form-input" onChange={handleChange}
                                    // value={this.state.mensaje} onChange={this.onChange} invalid={this.state.invalidMensaje}
                                    ></textarea>
                                    <label htmlFor="message" className="form-label"><FormattedMessage id="label-text-Contact" /></label>
                                </div>
                                <button type="submit" id="btn-one" onClick={submitForm}><FormattedMessage id="submit-text-Contact-home" /></button>
                            </fieldset>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    </>;

};

export default contact;