import React from 'react';
import { useEffect, useRef, useState } from 'react';
import flecha from '../icons/flecha.svg';
import { IntlProvider, FormattedMessage } from 'react-intl';

const Home = () => {
    const [height, setHeight] = useState(null);
    const [width, setWidth] = useState(null);
    const [show, setShow] = useState(false);
    window.addEventListener("scroll", function () {
        if (this.pageYOffset > 60) {
            document.querySelector("header").classList.add("sticky");
        }
        else {
            document.querySelector("header").classList.remove("sticky");
        }
    });

    const submitFormHome = (e) => {
        e.preventDefault();
        var valorFullNameHome = 0;
        var valortelephoneHome = 0;
        var valoremailHome = 0;
        var valorselectHome = 0;
        var valormessageHome = 0;
        var valorterminosHome = 0;

        if (document.querySelector("#full-name-home").value.length > 0) {
            valorFullNameHome = 1;
            document.querySelector("#full-name-home").classList.remove("error");
        }else{
            document.querySelector("#full-name-home").classList.add("error");
        }
        
        if (document.querySelector("#telephone-home").value.length > 0) {
            valortelephoneHome = 1;
            document.querySelector("#telephone-home").classList.remove("error");
        }else{
            document.querySelector("#telephone-home").classList.add("error");
        }

        if (document.querySelector("#mail-home").value.length > 0) {
            valoremailHome = 1;
            document.querySelector("#mail-home").classList.remove("error");
        }else{
            document.querySelector("#mail-home").classList.add("error");
        }

        if (document.querySelector("#your-motivo").value !== "Servicios solicitado:") {
            valorselectHome = 1;
            document.querySelector("#your-motivo").classList.remove("error");
        }else{
            document.querySelector("#your-motivo").classList.add("error");
        }

        if (document.querySelector("#message-home").value.length > 0) {
            valormessageHome = 1;
            document.querySelector("#message-home").classList.remove("error");
        }else{
            document.querySelector("#message-home").classList.add("error");
        }

        if (document.querySelector("#terminos-home").checked) {  
            valorterminosHome = 1;  
            document.querySelector("#terminos-home").classList.remove("error");
        }else{
            document.querySelector("#terminos-home").classList.add("error");
        }

        var result = valorFullNameHome * valortelephoneHome * valoremailHome * valorselectHome * valormessageHome *valorterminosHome;

        if (result == 0) {
            e.preventDefault();
        }else{
            document.querySelector("#fs-frm-home").submit();
        }
                
    }   

    window.addEventListener('click', function (e) {
        if (e.target.closest("#full-name-home")) {
            document.querySelector("#full-name-home").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#full-name-home").parentElement.classList.remove("focusin");
            if (document.querySelector("#full-name-home").value.length > 0) {
                document.querySelector("#full-name-home").parentElement.classList.add("ok");
            } else {
                document.querySelector("#full-name-home").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#telephone-home")) {
            document.querySelector("#telephone-home").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#telephone-home").parentElement.classList.remove("focusin");
            if (document.querySelector("#telephone-home").value.length > 0) {
                document.querySelector("#telephone-home").parentElement.classList.add("ok");
            } else {
                document.querySelector("#telephone-home").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#mail-home")) {
            document.querySelector("#mail-home").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#mail-home").parentElement.classList.remove("focusin");
            if (document.querySelector("#mail-home").value.length > 0) {
                document.querySelector("#mail-home").parentElement.classList.add("ok");
            } else {
                document.querySelector("#mail-home").parentElement.classList.remove("ok");
            }
        } 
        if (e.target.closest("#message-home")) {
            document.querySelector("#message-home").parentElement.classList.add("focusin");
        }else{
            document.querySelector("#message-home").parentElement.classList.remove("focusin");
            if (document.querySelector("#message-home").value.length > 0) {
                document.querySelector("#message-home").parentElement.classList.add("ok");
            } else {
                document.querySelector("#message-home").parentElement.classList.remove("ok");
            }
        } 
    })

    document.addEventListener("keydown", function(event) {
        if (event.keyCode == 9) {
            document.querySelector("#full-name-home").addEventListener('focusin', (event) => {
                document.querySelector("#full-name-home").parentElement.classList.add("focusin");
            });
            document.querySelector("#full-name-home").addEventListener('focusout', (event) => {
                document.querySelector("#full-name-home").parentElement.classList.remove("focusin");
                if (document.querySelector("#full-name-home").value.length > 0) {
                    document.querySelector("#full-name-home").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#full-name-home").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#telephone-home").addEventListener('focusin', (event) => {
                document.querySelector("#telephone-home").parentElement.classList.add("focusin");
            });
            document.querySelector("#telephone-home").addEventListener('focusout', (event) => {
                document.querySelector("#telephone-home").parentElement.classList.remove("focusin");
                if (document.querySelector("#telephone-home").value.length > 0) {
                    document.querySelector("#telephone-home").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#full-name-home").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#mail-home").addEventListener('focusin', (event) => {
                document.querySelector("#mail-home").parentElement.classList.add("focusin");
            });
            document.querySelector("#mail-home").addEventListener('focusout', (event) => {
                document.querySelector("#mail-home").parentElement.classList.remove("focusin");
                if (document.querySelector("#mail-home").value.length > 0) {
                    document.querySelector("#mail-home").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#mail-home").parentElement.classList.remove("ok");
                }
            });
            document.querySelector("#message-home").addEventListener('focusin', (event) => {
                document.querySelector("#message-home").parentElement.classList.add("focusin");
            });
            document.querySelector("#message-home").addEventListener('focusout', (event) => {
                document.querySelector("#message-home").parentElement.classList.remove("focusin");
                if (document.querySelector("#message-home").value.length > 0) {
                    document.querySelector("#message-home").parentElement.classList.add("ok");
                } else {
                    document.querySelector("#message-home").parentElement.classList.remove("ok");
                }
            });
        }
    })

    function handleChange (){
        console.log('asasdasd');
        if (document.querySelector("#full-name-home").value.length > 0) {
            document.querySelector("#full-name-home").parentElement.classList.add("ok");
        } else {
            document.querySelector("#full-name-home").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#telephone-home").value.length > 0) {
            document.querySelector("#telephone-home").parentElement.classList.add("ok");
        } else {
            document.querySelector("#telephone-home").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#mail-home").value.length > 0) {
            document.querySelector("#mail-home").parentElement.classList.add("ok");
        } else {
            document.querySelector("#mail-home").parentElement.classList.remove("ok");
        }
        if (document.querySelector("#message-home").value.length > 0) {
            document.querySelector("#message-home").parentElement.classList.add("ok");
        } else {
            document.querySelector("#message-home").parentElement.classList.remove("ok");
        }
    }

    return <>
        <section className="home" id="home">
            <div className="container">
                <div className="content" >
                    <div className='part1' data-aos="fade-up">
                        <h1 id='one' className='one'>Agencia <span>de Diseo</span></h1>
                        <h1 id='two' className='two'>Design <span>Agency</span></h1>
                        <p><FormattedMessage id="bajada-home" /></p>
                    </div>
                    <div className='part2' data-aos="fade-up">
                        <div className='content-form'>
                            <h2><FormattedMessage id="titulo-contacto-home" /></h2>
                            <p><FormattedMessage id="parrafo-contacto-home" /></p>
                            <form className="formulario containerform" id="fs-frm-home" name="complaint-form" acceptCharset="utf-8" action="../php/thanks.php" method="post">
                                <fieldset>
                                    <div className="form-group">
                                        <input type="text" name="full-name" id='full-name-home' placeholder="" className="form-input" onChange={handleChange} />
                                        <label htmlFor="full-name-home" className="form-label"><FormattedMessage id="label-name-Contact-home" /></label>
                                    </div>
                                    <div className="form-group">
                                        <input type="number" name="telephone" id='telephone-home' placeholder="" className="form-input" onChange={handleChange} />
                                        <label htmlFor="telephone-home" className="form-label"><FormattedMessage id="label-tel-Contact-home" /></label>
                                    </div>
                                    <div className="form-group">
                                        <input type="mail" name="email" id='mail-home' placeholder="" className="form-input" onChange={handleChange} />
                                        <label htmlFor="mail-home" className="form-label"><FormattedMessage id="label-email-Contact-home" /></label>
                                    </div>
                                    <div className="form-group group-select">
                                        <select name="seelctservicio" id="your-motivo">
                                            <option value="Servicios solicitado:"><FormattedMessage id="label-option1-Contact-home" /></option>
                                            <option value="Branding Diseo"><FormattedMessage id="label-option2-Contact-home" /></option>
                                            <option value="Desararrollo Web"><FormattedMessage id="label-option3-Contact-home" /></option>
                                        </select>
                                    </div>
                                    <div className="form-group group-textarea">
                                        <textarea rows="6" name="message" id='message-home' placeholder="" className="form-input" onChange={handleChange}/>
                                        <label htmlFor="message-home" className="form-label"><FormattedMessage id="label-text-Contact-home" /></label>
                                    </div>
                                    <div className="group-checkbox">
                                        <input type="checkbox"  name="terminos" id="terminos-home" />
                                        <label htmlFor="terminos-home" className="form-label"></label>
                                        {/* <label htmlFor="terminos" className="terminos-label"></label> */}
                                        <p><FormattedMessage id="politicas-text-Contact-home" /></p>
                                    </div>
                                    <button className='submit' onClick={submitFormHome}><FormattedMessage id="submit-text-Contact-home" /></button>
                                </fieldset>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <img className="flecha" src={flecha} alt="" />
        </section>
    </>;
};

export default Home;